@import url(https://fonts.googleapis.com/css2?family=Ultra&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Coming Soon", cursive;
}

.App {
  text-align: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/intermediary/f/61f7685f-47e5-49a0-b5cb-ca6b8cab4228/drok89-a7a2a550-ba87-40a6-9701-b635550d55b3.jpg");
  background-position: center center;
}

.Hangman {
  width: 400px;
  color: black;
  text-align: center;
  zoom: 1.3;
  font-family: "Ultra", serif;
}

#Hangman-title {
  width: 400px;
  text-align: center;
  padding: 0;
  border: 0;
  box-shadow: 0 0 0;
}

.Hangman img {
  width: 500px;
}

.Hangman h1 {
  font-size: 4rem;
  font-weight: 100;
  margin-bottom: 0.1em;
  margin-top: 0;
}

.Hangman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0.2em 0;
  font-size: 2rem;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 250px;
  margin-top: 0;
}

.Hangman button {
  border: none;
  font-family: inherit;
  font-size: 1.5rem;
  cursor: pointer;
  width: 50px;
  height: 50px;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: black;
  color: #fff;
  box-shadow: 0 6px #8c8c8c;
  border-radius: 50px;
  padding-top: 2px;
}

.Hangman button:hover {
  background-color: #996600;
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  box-shadow: 0 6px #9e9e9e;
}

#reset {
  width: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background: #1dafa1;
  box-shadow: 0 6px #e6c300;
}

#reset:hover {
  background-color: #996600;
}

